.card {
  padding: 50px;
}

.display-text-small {
  font-size: 2.0625rem;
  font-weight: 600;
  line-height: 1.2121;
}

.h1-small {
  font-size: 1.6875rem;
  font-weight: bold;
  line-height: 1.1852;
}

.h2-small {
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.333;
}

table {
  width: 100%;

  th {
    font-size: rem(12);
    font-weight: bold;
    line-height: 1.333;
    padding: 10px;
    text-align: left;
    text-transform: uppercase;
  }

  td {
    padding: 25px 10px;
  }

  tbody {
    td {
      border-bottom: 1px solid rgba(#0a1414, 0.16);
    }

    .large {
      td {
        padding-bottom: 50px;
      }
    }

    tr:last-child {
      td {
        border-bottom: none;
      }
    }
  }

  p {
    margin: 0;
  }
}

.th-height {
  min-width: 100px;
}

.th-context {
  width: 300px;
}
